<template><section>
<h1>Tag 标签</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来选择tag的类型，也可以通过<code v-pre class="code_inline">color</code>属性来自定义背景色。</p>
<div class="demo-box">
  <div class="demo-block">
    <j-tag>标签一</j-tag>
    <j-tag color="success">标签二</j-tag>
    <j-tag color="info">标签三</j-tag>
    <j-tag color="warning">标签四</j-tag>
    <j-tag color="danger">标签五</j-tag>
  </div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <j-tag>标签一</j-tag>
  <j-tag color="success">标签二</j-tag>
  <j-tag color="info">标签三</j-tag>
  <j-tag color="warning">标签四</j-tag>
  <j-tag color="danger">标签五</j-tag>
</div>
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span>&gt;</span>标签一<span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span>&gt;</span>标签三<span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>标签四<span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span>&gt;</span>标签五<span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</div></demo-block>
</div>
<h3>可移除标签</h3>
<p>设置<code v-pre class="code_inline">closable</code>属性可以定义一个标签是否可移除。默认的标签移除时会附带渐变动画，它接受一个Boolean，true 为关闭。</p>
<div class="demo-box">
  <div class="demo-block">
      <j-tag :key="index" v-for="(tag, index) in dynamicTags" closable :disable-transition="false" @close="handleClose(tag)" :color="tag.color">{{tag.name}}
    </j-tag>
    
    </div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <j-tag :key="index" v-for="(tag, index) in dynamicTags" closable :disable-transition="false" @close="handleClose(tag)" :color="tag.color">{{tag.name}}
  </j-tag>
  
</div>
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">j-tag</span>
    <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span> 
    <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(tag, index) in dynamicTags"</span> 
    <span class="hljs-attr">closable</span> <span class="hljs-attr">:disable-transition</span>=<span class="hljs-string">"false"</span> 
    <span class="hljs-attr">@close</span>=<span class="hljs-string">"handleClose(tag)"</span> 
    <span class="hljs-attr">:color</span>=<span class="hljs-string">"tag.color"</span>&gt;</span>{{tag.name}}
  <span class="hljs-tag">&lt;/<span class="hljs-name">j-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      data() {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">dynamicTags</span>: [{
              <span class="hljs-attr">name</span>: <span class="hljs-string">'标签一'</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'primary'</span>
            },
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">'标签二'</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'success'</span>
            },
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">'标签三'</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'info'</span>
            },
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">'标签四'</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'danger'</span>
            }
          ]
        }
      },
      <span class="hljs-attr">methods</span>: {
        handleClose(tag) {
          <span class="hljs-keyword">this</span>.dynamicTags.splice(<span class="hljs-keyword">this</span>.dynamicTags.indexOf(tag), <span class="hljs-number">1</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</div></demo-block>
 </div>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>color</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .j__tag{
    margin-right: 10px;
  }
</style>
<script>
      export default {
        data() {
          return {
            dynamicTags: [
              {
                name: '标签一',
                color: 'primary'
              },
              {
                name: '标签二',
                color: 'success'
              },
              {
                name: '标签三',
                color: 'info'
              },
              {
                name: '标签四',
                color: 'danger'
              }
            ]
          }
        },
        methods: {
          handleClose(tag) {
            this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
          }
        }
      }
    </script>


