<template><section>
<h2>Card 卡片</h2>
<p>将信息聚合在卡片容器中展示</p>
<h3>基础用法</h3>
<p>包括标题,内容和操作<br>
Card 组件包括<code v-pre class="code_inline">header</code>和<code v-pre class="code_inline">body</code>部分，<code v-pre class="code_inline">header</code>部分需要有显式具名 slot 分发，同时也是可选的。</p>
<div class="demo-box">
  <div class="demo-block">
    <j-card class="box-card">
      <div slot="header" class="clearfix">
        <span>卡片名称</span> 
        <j-button style="float: right; padding: 3px 0px" type="text">操作按钮</j-button>
      </div>
      <div v-for="o in 4" :key="o" class="text item">
        {{'列表内容 ' + o}}
      </div>
    </j-card>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><j-card class="box-card">
