    {{'列表内容 ' + o}}
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">j-card</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>带图片</h3>
<p>可配置定义更丰富的内容展示,
配置<code v-pre class="code_inline">body-style</code>属性来自定义<code v-pre class="code_inline">body</code>部分的<code v-pre class="code_inline">style</code>，我们还使用了布局组件。</p>
<div class="demo-box">
  <div class="demo-block">
    <j-row>
      <j-col :span="8" v-for="(o, index) in 2" :key="o" :offset="index > 0 ? 2: 0">
        <j-card :body-style="{padding: '0px'}">
            <img src="../assets/img/pic.png" alt class="image">
            <div style="padding: 14px;">
              <span>外滩风景</span>
              <div class="bottom clearfix">
                <time class="time">{{currentDate}}</time>
                <j-button type="text" class="button">按钮</j-button>
              </div>
            </div>
        </j-card>
      </j-col>
    </j-row>
  </div>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><j-row>
  <j-col :span="8" v-for="(o, index) in 2" :key="o" :offset="index > 0 ? 2: 0">
    <j-card :body-style="{padding: '0px'}">
      <img src="../assets/img/pic.png" alt class="image">
      <div style="padding: 14px;">
        <span>外滩风景</span>
        <div class="bottom clearfix">
          <time class="time">{{currentDate}}</time>
          <j-button type="text" class="button">按钮</j-button>
