</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>卡片阴影</h3>
<p>可对阴影的显示进行配置,通过<code v-pre class="code_inline">shadow</code>属性设置卡片阴影出现的时机：<code v-pre class="code_inline">always</code>、<code v-pre class="code_inline">hover</code>或<code v-pre class="code_inline">never</code>。</p>
<div class="demo-box">
  <div class="demo-block">
    <j-row :gutter="12">
      <j-col :span="8">
        <j-card shadow="always">
          总是显示
        </j-card>
      </j-col>
      <j-col :span="8">
        <j-card shadow="hover">
          鼠标悬浮时显示
        </j-card>
      </j-col>
      <j-col :span="8">
        <j-card shadow="never">
          从不显示
        </j-card>
      </j-col>
    </j-row>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><j-row :gutter="12">
  <j-col :span="8">
    <j-card shadow="always">
      总是显示
    </j-card>
  </j-col>
  <j-col :span="8">
    <j-card shadow="hover">
      鼠标悬浮时显示
    </j-card>
  </j-col>
  <j-col :span="8">
    <j-card shadow="never">
      从不显示
    </j-card>
  </j-col>
</j-row>
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">j-row</span> <span class="hljs-attr">:gutter</span>=<span class="hljs-string">"12"</span>&gt;</span>
