      从不显示
    <span class="hljs-tag">&lt;/<span class="hljs-name">j-card</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">j-col</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">j-row</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>header</td>
<td>设置 header，也可以通过 <code v-pre class="code_inline">slot#header</code> 传入 DOM</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>body-style</td>
<td>设置 body 的样式</td>
<td>object</td>
<td>—</td>
<td>{ padding: '20px' }</td>
</tr>
<tr>
<td>shadow</td>
<td>设置阴影显示时机</td>
<td>string</td>
<td>always / hover / never</td>
<td>always</td>
</tr>
</tbody>
</table>
</section></template>
<style scoped>
  .text {
    font-size: 14px;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .item {
    margin-bottom: 18px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }
  .box-card {
    width: 480px;
  }
</style>
<script>
export default {
  data() {
    return {
      currentDate: new Date()
    };
  }
}
</script>


