<template><section><h1>Twotable 二维表格</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">data</code>属性来绑定数据，x属性设置列名, y属性设置行名。</p>
<div class="demo-box">
  <div class="demo-block">
<two-table :data="tableData" x="size" y="color" xy-value="quantity" x-header="颜色" y-header="尺码" style="width:500px;"></two-table>
  </div>
</div>

<demo-block>
                        <div slot="desc"><html><head><template>
  <two-table :data="tableData" x="size" y="color" xy-value="quantity" x-header="颜色" y-header="尺码" style="width:500px;"></two-table>
</template>

</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">two-table</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"tableData"</span> <span class="hljs-attr">x</span>=<span class="hljs-string">"size"</span> <span class="hljs-attr">y</span>=<span class="hljs-string">"color"</span> <span class="hljs-attr">xy-value</span>=<span class="hljs-string">"quantity"</span> <span class="hljs-attr">x-header</span>=<span class="hljs-string">"颜色"</span> <span class="hljs-attr">y-header</span>=<span class="hljs-string">"尺码"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:500px;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">two-table</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
 <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
   data () {
     <span class="hljs-keyword">return</span> {
       <span class="hljs-attr">tableData</span>: [
          {<span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'S'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">100</span>},
          { <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'M'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">60</span> },
          { <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'L'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">30</span> },
          { <span class="hljs-attr">color</span>: <span class="hljs-string">'blue'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'M'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">90</span> },
          { <span class="hljs-attr">color</span>: <span class="hljs-string">'blue'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'L'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">20</span> },
          { <span class="hljs-attr">color</span>: <span class="hljs-string">'blue'</span>, <span class="hljs-attr">size</span>: <span class="hljs-string">'S'</span>, <span class="hljs-attr">quantity</span>: <span class="hljs-number">80</span> }
       ]
     }
   }
 }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>是否必填</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>显示数据</td>
<td>array</td>
<td>—</td>
<td>必填</td>
</tr>
<tr>
<td>x</td>
<td>列名</td>
<td>string</td>
<td>—</td>
<td>必填</td>
</tr>
<tr>
<td>y</td>
<td>行名</td>
<td>string</td>
<td>—</td>
<td>必填</td>
</tr>
<tr>
<td>xy-value</td>
<td>表格单元要显示的数据名</td>
<td>string</td>
<td>—</td>
<td>必填</td>
</tr>
<tr>
<td>x-header</td>
<td>x向表头名</td>
<td>string</td>
<td>—</td>
<td>选填</td>
</tr>
<tr>
<td>y-header</td>
<td>y向表头名</td>
<td>string</td>
<td>—</td>
<td>选填</td>
</tr>
</tbody>
</table>
</section></template>

<script>
 export default{
   data () {
     return {
       tableData: [
          {color: 'red', size: 'S', quantity: 100},
          { color: 'red', size: 'M', quantity: 60 },
          { color: 'red', size: 'L', quantity: 30 },
          { color: 'blue', size: 'M', quantity: 90 },
          { color: 'blue', size: 'L', quantity: 20 },
          { color: 'blue', size: 'S', quantity: 80 }
       ]
     }
   }
 }
</script>


